import React from 'react';
import styles from './Article.module.css';
import VideoMP4 from '../videoMP4';

const page = () => {
  return (
    <div className={styles.article}>
        <div>
            <p>Les codes de ces projets ont été réalisés en C, en utilisant une librairie développée par mon professeur pour simplifier l'utilisation de la SDL2. La SDL2 (Simple DirectMedia Layer) est une bibliothèque qui facilite le développement de jeux et d'applications multimédia. Elle permet la gestion des graphiques, du son, des entrées (clavier, souris, manettes) et d'autres fonctionnalités nécessaires pour créer des jeux en 2D.</p>
        </div>

        <h3 className={styles.subtitle}>Création d'un Logo et développement d'un Démineur</h3>
        <div className={styles.paragraph_100}>
            
            <p>Le premier projet en ISN consistait à concevoir un logo personnalisé. L'objectif était de comprendre les bases de la conception graphique 2D. </p>
            <p>Puis le second projet imposé était le développement d'un jeu de démineur. Inspiré du célèbre jeu, il fallait créer une grille où les joueurs doivent éviter des mines en révélant les cases. Ce projet m'a initié à des concepts clés de la programmation, tels que les algorithmes de génération aléatoire, la gestion d'événements, et la mise en place de conditions de victoire et de défaite.</p>
        </div>
        <div className= {`${styles.paragraph_100} ${styles.margincontent}`}>
            <VideoMP4 filePath={`${process.env.PUBLIC_URL}/data/video/Démineur.mp4`} />
        </div>
        <h3 className={styles.subtitle}>Conception d'un Jeu de Tape-Taupe </h3>
        <div className={styles.paragraph_100}>
            <p>Pour le projet de fin d'année qui était un sujet libre, j'ai choisi avec l'un de mes camarade de lycée de développer un jeu de tape-taupe. L'objectif était de concevoir un jeu interactif avec une fausse 3D, bien que le moteur utilisé ne supportait que la 2D. Le jeu simulait une profondeur en jouant sur les tailles, les positions des taupes et les fonction trigonométrique. </p>
        </div>
        
        <VideoMP4 filePath={`${process.env.PUBLIC_URL}/data/video/TapeTaupe.mp4`} />

        <div className= {`${styles.paragraph_50} ${styles.padding1em}`} >

            <h3 className={styles.subtitle}>Fonctionnement de la Fausse 3D</h3>
            <p>Pour créer une illusion de profondeur en 2D dans mon jeu de tape-taupe, j'ai utilisé une approche mathématique pour simuler la perspective. En particulier, j'ai appliqué les fonctions <code>cos(x - sin(x)*P) et sin(x - sin(x)*P)</code> pour ajuster la taille et la position non seulement des taupes, mais aussi du terrain, dont P est une valeur entre [0,1] qui represente l'effect de profondeur.</p>
            <p>Ces fonctions modifie les dimensions et la disposition des éléments à l'écran en fonction de leur position. Les fonctions <code>cos(x - sin(x)*P) et sin(x - sin(x)*P)</code> génère des valeurs qui varient de manière non linéaire, ce qui permet de donner l'impression que les taupes et le terrain se rapprochent ou s'éloignent, créant ainsi une illusion de profondeur. Cette technique permet de simuler une perspective tridimensionnelle malgré les limitations du moteur graphique, qui ne prend en charge que la 2D.</p>
            
        </div> 
        <div className={styles.paragraph_100}>
            <p>J'ai inventé cette fonction en cherchant un moyen simple de créer cette illusion. En explorant les propriétés des fonctions trigonométriques non linéaires comme <code>cos</code> et <code>sin</code>, j'ai remarqué que leurs formes et variations pouvaient être utilisées pour créer des effets intéressants. En particulier, j'ai observé que les périodes positives et négatives de ces fonctions créaient des variations distinctes. Après avoir expérimenté avec différentes combinaisons de fonctions, j'ai trouvé que <code>cos(x - sin(x))</code> offrait une solution efficace pour simuler une perspective en 2D, grâce à ses caractéristiques uniques de variation.</p>
        </div> 
        
        <div className={`${styles.paragraph_50} ${styles.margincontent}`}>
            <code className={styles.maxcontent}>cos(x) et sin(x)</code>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/TPcosx.png'} alt="TPcosx.png"></img>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/Trigo.png'} alt="Trigo.png"></img>
        </div>
        <div className={`${styles.paragraph_50} ${styles.margincontent}`}>
            <code className={styles.maxcontent}>cos(x - sin(x)*P) et sin(x - sin(x)*P)</code>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/TPcosx2.png'} alt="TPcosx.png"></img>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/Trigo2.png'} alt="Trigo2.png"></img>
        </div>

        <h3 className={styles.subtitle} >Rétrospective de la Technique</h3>

        <div className={styles.paragraph_100}>
            
            <p>Bien que la technique de fausse 3D basée sur les fonctions <code>cos(x - sin(x)*P) et sin(x - sin(x)*P)</code> soient intéressantes et offre une première approche des notions de perspective, elles présentent des limitations importantes. En réalité, le calcul n'est pas particulièrement performant et les fonctions sont utilisées pour modifier des valeurs telles que l'angle de vue et la profondeur, ce qui peut devenir complexe à ajuster correctement.</p>
            <p>Cette méthode reste très limitée comparée aux techniques de représentation 3D classiques. Elle ne peut pas remplacer la vraie 3D ni les autres méthodes plus sophistiquées de simulation de profondeur. Cependant, elle constitue une approche didactique intéressante pour comprendre les concepts de perspective et de profondeur en 2D, et peut servir de point de départ pour explorer des techniques plus complexes.</p>
        </div>
    </div>
  );
}

export default page;